
html,
body {
  padding: 0;
  margin: 0;
  height: 100%;
}

#map {
  margin: auto;
  height: 80%;
  width: 80%;
}






.dropdown {
  border: 1px solid black;
  background-color: white;
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 100;
  width: 100%;
}
.dropdown li {
  cursor: pointer;
  list-style-type: none;
  height: 50px;
  padding: 5px;
  display: flex;
  flex-direction: column;
}
.dropdown li span {
  font-weight: bold;
}
.dropdown li:hover {
  background-color: lightsteelblue;
}
.dropdown ul {
  margin: 0;
  padding: 0;
  width: 100%;
}
.panel {
  width: 800px;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.panel input[type=text] {
  height: 30px;
  border-radius: 5px;
  border: none;
  border: 1px solid lightgray;
  font-size: 15px;
}
.sep {
  margin-top: 20px;
}
.test {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
}